import { useState, useEffect } from 'react';
import './App.css';
import { BhAppShell, BhBreadcrumbs, BhContent } from '@baker-hughes-central-design/ui-toolkit-react/dist/components';
import { BrowserRouter as Router, useRoutes, useNavigate } from "react-router-dom";

import Dashboard from './components/pages/dashboard';
import Details from './components/pages/details';
import { ReactCustomElement } from 'web-components-with-react';
import reactToWebcomponent from 'react-to-webcomponent';
import React from "react";
import ReactDOM from "react-dom"

function App() {
  const [route, setRoute] = useState('/');
  const [notification, setNotification] = useState(2);
  const [iconLinksProp, setIconLinksProp] = useState([{ name: 'help_outline', key: 'tearsheet--icon-help' }, { name: 'notifications', 'badge': notification ? notification.toString() : '', key: 'tearsheet--icon-notifications' }]);

  const navigation = [
    { label: 'Dashboard', route: '/', icon: 'dashboard', key: 'tearsheet--nav--dashboard' },
    { label: 'Details', route: 'details', icon: 'flag', key: 'tearsheet--nav--details' }
  ];

  useEffect(() => {
    setIconLinksProp([{ name: 'help_outline', key: 'tearsheet--icon-help' }, { name: 'notifications', 'badge': notification ? notification.toString() : '', key: 'tearsheet--icon-notifications' }]);
  }, [notification]);

  const Contents = () => {
    const routes = useRoutes([
      { path: '/', element: <Dashboard onIncrementNotification={() => {
        setNotification(notification + 1);
      }} /> },
      { path: 'details', element: <Details /> }
    ]);
    const navigate = useNavigate();
    useEffect(() => {
      navigate(route);
    }, [route]);
    return routes;
  }

  return (
    <BhAppShell
      type={'primary'}
      appname={'Baker Hughes Web App'}
      userfirstname={'Hello'}
      userlastname={'World'}
      userimage={'https://www.christies.com/img/LotImages/2016/NYR/2016_NYR_12145_0013B_000(pablo_picasso_buste_de_femme105405).jpg'}
      useremail={'user.name@frogdesign.com'}
      navigation={navigation}
      navigationSelected={{ item: navigation.find(item => { return item.route === route }).key }}
      settings={[{ label: 'System Preferences', icon: 'tune', key: 'tearsheet--settings-menu--system-preferences' }, { label: 'Language', icon: 'language', key: 'tearsheet--settings-menu--language', 'submenu': [{ label: 'Albanian', key: 'tearsheet--settings-menu--language--albanian' }, { label: 'Arabic', key: 'tearsheet--settings-menu--language--arabic' }, { label: 'Bosnian', key: 'tearsheet--settings-menu--language--bosnian' }, { label: 'Bulgarian', key: 'tearsheet--settings-menu--language--bulgarian' }, { label: 'Catalan', key: 'tearsheet--settings-menu--language--catalan' }, { label: 'Chinese', key: 'tearsheet--settings-menu--language--chinese' }, { label: 'Danish', key: 'tearsheet--settings-menu--language--danish' }, { label: 'English', key: 'tearsheet--settings-menu--language--english' }, { label: 'Finnish', key: 'tearsheet--settings-menu--language--finnish' }, { label: 'French', key: 'tearsheet--settings-menu--language--french' }, { label: 'Hindi', key: 'tearsheet--settings-menu--language--hindi' }, { label: 'Italian', key: 'tearsheet--settings-menu--language--italian' }, { label: 'Japanese', key: 'tearsheet--settings-menu--language--japanese' }]}, { label: 'Metric System', icon: 'functions', key: 'tearsheet--settings-menu--metric-system', 'submenu': [{ label: 'Metric', key: 'tearsheet--settings-menu--metric--metric' }, { label: 'Imperial', key: 'tearsheet--settings-menu--metric--imperial' }]}, { label: 'Authentication', icon: 'security', key: 'tearsheet--settings-menu--authentication' }, { label: 'My Teams', icon: 'group', key: 'tearsheet--settings-menu--my-teams' }, { 'isDivider': true }, { label: 'Log Out', icon: 'power_settings_new', key: 'tearsheet--settings-menu--log-out'}]}
      settingsSelected={[{ item: 'tearsheet--settings-menu--language', submenuItem: 'tearsheet--settings-menu--language--english' }, { item: 'tearsheet--settings-menu--metric-system', submenuItem: 'tearsheet--settings-menu--metric--metric' }]}
      iconLinks={iconLinksProp}
      onBhEventSelected={(event) => {
        if (event.detail?.type === 'navigation') {
          const key = event.detail.keys?.item;
          const navigationItem = navigation.find((item) => { return item.key === key });
          setRoute(navigationItem.route);
        }
      }}
    >
      <BhBreadcrumbs
        slot={'bh-breadcrumbs'}
        items={[{ icon: 'public', key: 'breadcrumbs--item-0' }]}
      />
      <BhContent slot={'bh-content'}>
        <Router>
          <Contents />
        </Router>
      </BhContent>
    </BhAppShell>
  );
}

export default App;

window.customElements.define('test-app', reactToWebcomponent(App, React, ReactDOM));
